"use client";

import { useEffect, useState } from "react";
import Skeleton from "./Skeleton";

interface StockChartProps {
  ticker: string;
}

export default function StockChart({ ticker }: StockChartProps) {
  const [data, setData] = useState<any[]>([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (!ticker) return;

    setLoading(true);

    setTimeout(() => {
      const fakeData = [
        { time: "2023-01-01", price: 150 },
        { time: "2023-02-01", price: 160 },
        { time: "2023-03-01", price: 140 },
      ];
      setData(fakeData);
      setLoading(false);
    }, 1500);
    // Later: fetch real backend API here instead
  }, [ticker]);

  if (!ticker) {
    return (
      <div className="text-gray-500">
        Please search for a stock to see the chart.
      </div>
    );
  }
  if (loading) {
    return <Skeleton />;
  }

  return (
    <div className="mt-6">
      <h2 className="text-xl font-bold mb-2">
        Stock Chart for {ticker.toUpperCase()}
      </h2>
      <p>(Chart goes here â€” showing {data.length} points.)</p>

      {/* Later: Render real chart.js here */}
    </div>
  );
}
